@import "./navbar";
@import "./reader";
@import "./viewer";

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, 
        "Segoe UI", Roboto, "Helvetica Neue", 
        Arial, sans-serif;
}
body{  
    display: flex;
    padding: 150px;
    min-height: 100vh;
    align-items: center;
    justify-content: center;

    --primary-color: #00aee8;
    --body-bg: #ffffff;
    --card-bg: #fcfcfc;
    --border-color: #eaecef;
    --title-color: #293b50;
    --text-color: #293b50;
    background-color: var(--body-bg);

    &.dark{
        --primary-color: #00c0d5;
        --body-bg: #2c3033;
        --card-bg: #292d30;
        --border-color: #4b505c;
        --title-color: #cccccc;
        --text-color: #8e8f90;
    }
}