.viewer{
    overflow: hidden;
    display: flex;
    flex-direction: column;
    position: relative;
    width: 1050px;
    word-break: break-word;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    background-color: var(--card-bg);
    box-shadow: 0 5px 7.5px -1px rgba(0, 0, 0, 0.1), 0 2.5px 5px -2px rgba(0, 0, 0, 0.1);

    a{
        text-decoration: none;
        color: var(--primary-color);
    
        &:hover{
            text-decoration: underline;
        }
    }
    header{
        margin: 25px 2% 50px 2%;
        width: 96%;
        line-height: 50px;
        font-size: 2.1em;
        font-weight: 500;
        border-bottom: 2px solid var(--border-color);
        color: var(--title-color);
    }
    .errors{
        padding: 2%;
        width: 96%;
    }
    .plist-data{
        padding: 2%;
        width: 96%;

        h2, h3{
            color: var(--title-color);
            margin-top: 5px 0 5px 0;
        }
    }
    p{
        padding-left: 25px;
        align-items: center;
        line-height: 25px;
        color: var(--text-color);
    }
}
@media screen and (max-width: 1300px){
    .viewer{
        width: 80vw;
    }
}
