nav{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 3.6rem;
    border-bottom: 1px solid var(--border-color);
    background-color: var(--body-bg);


    a{
        text-decoration: none;
        color: inherit;
    }
    label{
        cursor: pointer;
        margin-left: 25px;
        line-height: 3.6rem;
        font-size: 21px;
        font-weight: 500;
        color: var(--title-color);
    }
    ul{
        float: right;
        margin-right: 5px;
        height: 100%;
        list-style-type: none;

        li{
            cursor: pointer;
            display: inline-block;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            margin-right: 25px;
            line-height: 1.8rem;
            font-size: 15px;
            border-bottom: 2px solid transparent;
            color: var(--title-color);

            &:first-child:hover{
                border-bottom: 2px solid var(--primary-color);
            }
        }
    }
}